import { Button, Container, IconButton } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import DeleteIcon from "@material-ui/icons/Delete";
function Staff() {
  const history = useHistory();

  const [Staff, setStaff] = useState(
    JSON.parse(localStorage.getItem("staff_data"))
      ? JSON.parse(localStorage.getItem("staff_data"))
      : []
  );

  useEffect(() => {
    localStorage.setItem("staff_data", JSON.stringify(Staff));
  }, [Staff]);

  const addStaff = () => {
    const name = prompt("Add Member");
    if (!name) return;
    const newStaff = [...Staff, { name: name, kaam: [] }];
    setStaff(newStaff);
  };

  const removeStaff = (name) => {
    const newStaff = Staff.filter((item) => item.name !== name);
    setStaff(newStaff);
  };

  return (
    <Container style={{ paddingLeft: 30, paddingRight: 15 }}>
      {Staff &&
        Staff.map((item, index) => (
          <div
            key={index}
            style={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            <h3 onClick={() => history.push(`/staff/${item.name}`)}>
              {item.name}
            </h3>
            <IconButton
              color="primary"
              component="span"
              onClick={() => removeStaff(item.name)}
            >
              <DeleteIcon />
            </IconButton>
          </div>
        ))}
      <br />

      <Button
        style={{ position: "absolute", bottom: 25, right: 25 }}
        color="primary"
        variant="contained"
        onClick={() => addStaff()}
      >
        Add Member
      </Button>
    </Container>
  );
}

export default Staff;
