import {
  Container,
  Grid,
  List,
  ListItem,
  ListItemText,
  makeStyles,
  Paper,
} from "@material-ui/core";
import React from "react";
import { Link as RouterLink, useHistory } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
}));
function ListItemLink(props) {
  const { primary, to } = props;

  const renderLink = React.useMemo(
    () =>
      React.forwardRef((itemProps, ref) => (
        <RouterLink to={to} ref={ref} {...itemProps} />
      )),
    [to]
  );

  return (
    <li>
      <ListItem button component={renderLink}>
        <ListItemText primary={primary} />
      </ListItem>
    </li>
  );
}

function Item({ data }) {
  const classes = useStyles();
  const history = useHistory();

  return (
    <Container>
      <div
        style={{
          display: "flex",
          width: "100%",
          justifyContent: "space-evenly",
        }}
      >
        <div
          style={{
            flex: 0.5,
            backgroundColor: "#e83328",
            color: "white",
            alignItems: "center",
            justifyContent: "center",
            textAlign: "center",
            padding: 15,
            margin: 10,
          }}
        >
          {data.cat}
        </div>
        <div
          style={{
            flex: 0.5,
            backgroundColor: "#e83328",
            color: "white",
            alignItems: "center",
            justifyContent: "center",
            textAlign: "center",
            padding: 15,
            margin: 10,
          }}
        >
          {data.date.toDate().getDate()} / {data.date.toDate().getMonth() + 1} /{" "}
          {data.date.toDate().getFullYear()}
        </div>
      </div>
      <div
        style={{
          flex: 1,
          border: "3px solid #e83328",
          alignItems: "center",
          justifyContent: "center",
          textAlign: "center",
          padding: 15,
          margin: 10,
        }}
      >
        मा. शिल्लक : {data.previous_rem}
      </div>
      <div style={{ display: "flex" }}>
        <div
          style={{
            flex: 0.5,
            backgroundColor: "#e83328",
            color: "white",
            alignItems: "center",
            justifyContent: "center",
            textAlign: "center",
            padding: 15,
            margin: 10,
          }}
          onClick={() => history.push(`/intable/${data.id}`)}
        >
          दिला : {data.total_in.value}
        </div>
        <div
          style={{
            flex: 0.5,
            backgroundColor: "#e83328",
            color: "white",
            alignItems: "center",
            justifyContent: "center",
            textAlign: "center",
            padding: 15,
            margin: 10,
          }}
          onClick={() => history.push(`/outtable/${data.id}`)}
        >
          एकूण : {data.total}
        </div>
        <div
          style={{
            flex: 0.5,
            backgroundColor: "#e83328",
            color: "white",
            alignItems: "center",
            justifyContent: "center",
            textAlign: "center",
            padding: 15,
            margin: 10,
          }}
          onClick={() => history.push(`/outtable/${data.id}`)}
        >
          जावक : {data.total_out.value}
        </div>
      </div>
      <div
        style={{
          flex: 0.5,
          alignItems: "center",
          border: "3px solid #e83328",
          justifyContent: "center",
          textAlign: "center",
          padding: 15,
          margin: 10,
        }}
      >
        शिल्लक : {data.rem}
        <br />
        <br />
        <span>
          {data.previous_rem} +{" "}
          <span style={{ color: "green" }}>{data.total_in.value}</span> -{" "}
          <span style={{ color: "red" }}>{data.total_out.value}</span> ={" "}
          {data.rem}
        </span>
      </div>
    </Container>
  );
}

export default Item;
