import { Button, Container, IconButton } from "@material-ui/core";
import { ControlPointSharp } from "@material-ui/icons";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import DeleteIcon from "@material-ui/icons/Delete";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import CheckCircleOutlineIcon from "@material-ui/icons/CheckCircleOutline";

function StaffDetails() {
  const { id } = useParams();
  const [kaam, setKaam] = useState([]);

  useEffect(() => {
    if (id) {
      let staff_data = JSON.parse(localStorage.getItem("staff_data"));
      staff_data = staff_data.filter((item) => item.name === id)[0];
      setKaam(staff_data.kaam);
    }
  }, []);

  useEffect(() => {
    if (kaam) {
      let staff_data = JSON.parse(localStorage.getItem("staff_data"));
      const index = staff_data.findIndex((item) => item.name === id);
      staff_data[index].kaam = kaam;
      localStorage.setItem("staff_data", JSON.stringify(staff_data));
    }
  }, [kaam]);

  const addKaam = () => {
    const newKaam = prompt("Add Task");
    if (!newKaam) return;
    setKaam([...kaam, newKaam]);
  };

  const deleteKaam = (id) => {
    const newKaam = kaam.filter((item) => item !== id);
    setKaam(newKaam);
  };

  return (
    <Container style={{ paddingLeft: 30, paddingRight: 15 }}>
      <h3>{id}</h3>
      {kaam &&
        kaam.map((item, index) => (
          <div
            key={index}
            style={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
            }}
          >
            {item}
            <IconButton
              color="primary"
              component="span"
              onClick={() => deleteKaam(item)}
            >
              <CheckCircleOutlineIcon />
            </IconButton>
          </div>
        ))}
      <Button
        style={{ position: "absolute", bottom: 25, right: 25 }}
        color="primary"
        variant="contained"
        onClick={addKaam}
      >
        Add Task
      </Button>
    </Container>
  );
}

export default StaffDetails;
